import { Container, Typography } from "@material-ui/core";
import { useEffect } from "react";
import { useState } from "react";

import axios from 'axios'

const HandicapList = () => {

    const [hcList, setHcList] = useState([])

    useEffect(() => {
        axios.get('https://risbeegomfkerho.herokuapp.com/api/courses').then((res) => {
            setHcList(res.data)
        })
    }, [])
    return (
        <Container>
            <Typography variant="h3">Handicapit</Typography>
            <ul>
                {hcList.map(i => <Rata key={i.name+i.layout} rata={i} />)}
            </ul>
        </Container>
    )
}
const Rata = ({ rata }) => {
    return (
        <div>
            <li>
                {rata.name} / {rata.layout}
                <ul>
                    {rata.players.map(p => <Pelaaja key={p.name+rata.name+rata.layout} pelaaja={p} /> )}
                </ul>
            </li>
        </div>
    )
}
const Pelaaja = ( { pelaaja }) => {
    return (
        <li>
            <strong>{pelaaja.name}</strong>, pelejä: {pelaaja.games}, juokseva HC: {pelaaja.runningHC}, HC: <strong>{pelaaja.HC}</strong>
        </li>
    )
}
export default HandicapList;