import React, { useState } from 'react'
import { Typography, Box, Container, Button, CircularProgress } from '@material-ui/core'
import { Link } from 'react-router-dom'

const FrontPage = () => {
  const [laskuri, setLaskuri] = useState(-1)
  const handleClick = () => {
    setLaskuri(0)
    ajastus(laskuri)
  }
  const ajastus = (n) => {
    setLaskuri(n + 1)
    if (n < 3) {
      setTimeout(() => {
        ajastus(n + 1)
      }, Math.floor(Math.random() * 5000))
    }
  }
  return (
    <Container>
      <Box textAlign="center">
         <Link to="/games">Kilpailut</Link> | <Link to="/hc">Handicapit</Link> | <Link to="/rawData">Raakadata</Link>
        <Typography variant="h4" style={{ marginBottom: 60}}>
          Risbeegomfkerho
        </Typography>
        <Typography variant="body1">
          Paina nappia selvittääksesi kuka on Risbeegomfkerhon vuoden 2021 paras pelaaja.
        </Typography>
        <div style={{ marginTop: 50 }}>
          <Laskuri laskuri={laskuri} handleClick={handleClick} />
        </div>
      </Box>
    </Container >

  )
}
const Laskuri = ({ laskuri, handleClick }) => {
  const tekstit = [
    "Lasketaan tasoituksia",
    "Pisteiden derivointia",
    "Miinustetaan Antin tarinat",
    "Otetaan mörrikerroin huomioon",
    "Henkka"
  ]

  if (laskuri === -1)
    return <Button variant="contained" color="primary" onClick={handleClick}>Laske kerhon paras pelaaja</Button>
  else return (
    <div>
      {(laskuri < tekstit.length - 1)
        ? <><CircularProgress /><Typography variant="h5">{tekstit[laskuri]}</Typography></>
        : <><Typography variant="h5">Paras pelaaja on <strong>Henkka</strong></Typography></>
      }
    </div>
  )

}
export default FrontPage